/**
 * @jest-environment jsdom
 */
import { renderBridgeHook } from './test-utils/index';
import { useBridgeLayer } from '../src/hooks/useBridgeLayer';

// Mock business layer dependencies
jest.mock('../src/business-layer', () => ({
  useTryonMutation: jest.fn(() => ({
    mutate: jest.fn(),
    mutateAsync: jest.fn(),
    data: undefined,
    error: null,
    isError: false,
    isSuccess: false,
    isLoading: false,
    reset: jest.fn(),
  })),
  useImageProcessing: jest.fn(() => ({
    processImage: jest.fn(),
    processBasic: jest.fn(),
    isProcessing: false,
    error: null,
  })),
  useImageValidation: jest.fn(() => ({
    data: { isValid: true, errors: [] },
    error: null,
    isLoading: false,
    isError: false,
  })),
  useImageMetadata: jest.fn(() => ({
    data: { dimensions: { width: 1024, height: 768 }, format: 'image/jpeg', size: 102400 },
    error: null,
    isLoading: false,
    isError: false,
  })),
  useImageThumbnail: jest.fn(() => ({
    data: 'data:image/jpeg;base64,mock-thumbnail',
    error: null,
    isLoading: false,
    isError: false,
  })),
  processImageForTryon: jest.fn(),
  resizeImageTo1024x1536: jest.fn(),
}));

describe('Debug useBridgeLayer Simple', () => {
  it('should render useBridgeLayer without returning null', () => {
    const { result } = renderBridgeHook(() => useBridgeLayer());

    console.log('Result current:', result.current);
    console.log('Result current type:', typeof result.current);
    console.log('Result current keys:', result.current ? Object.keys(result.current) : 'null');

    expect(result.current).not.toBeNull();
    expect(result.current).toBeDefined();
  });

  it('should have state, actions and advanced properties', () => {
    const { result } = renderBridgeHook(() => useBridgeLayer());

    expect(result.current).toBeTruthy();
    expect(result.current.state).toBeDefined();
    expect(result.current.actions).toBeDefined();
    expect(result.current.advanced).toBeDefined();
  });
});